<template>
  <div class="contain">
    <div class="main">
      <ul>
        <li v-for="item,index in list" @click="show(index)">
          <div class="cover">
            <img :src="item.imgsList[0]" alt="">
            <div class="des">
              <p>名字：{{item.name}}</p>
              <p>描述：{{item.des}}</p>
            </div>
          </div>
        </li>
        </script>
      </ul>
    </div>
    <div class="detail" v-if="flag">
      <div class="focus">
        <button class="del" @click="show"></button>
        <img :src="list[idx1].imgsList[idx2]" alt="">
        <button class="btn btn_l" @click="pre"></button>
        <button class="btn btn_r" @click="next"></button>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import "../assets/js/photo";
import Axios from "axios";
export default {
  // created() {
  //   // alert(this.test);
  //   Axios.post("/getphoto").then(res => {
  //     console.log(res.data);
  //     this.list = res.data;
  //     console.log(this.list);
  //   });
  // },
  data() {
    //这里存放数据
    return {
      // test: 123,
      list: [
        {
          name: "dnf",
          des: "游戏美图",
          imgsList: [
            require("../assets/img/1-1.jpg"),
            require("../assets/img/1-2.jpg"),
            require("../assets/img/1-3.jpg"),
            require("../assets/img/1-4.jpg"),
            require("../assets/img/1-5.jpg"),
            require("../assets/img/1-6.jpg"),
            require("../assets/img/1-7.jpg"),
            require("../assets/img/1-8.jpg")
          ]
        },
        {
          name: "南京夫子庙",
          des: "2019年dnf嘉年华之阿拉德市集活动",
          imgsList: [
            require("../assets/img/2-1.jpg"),
            require("../assets/img/2-2.jpg"),
            require("../assets/img/2-3.jpg"),
            require("../assets/img/2-4.jpg"),
            require("../assets/img/2-5.jpg"),
            require("../assets/img/2-6.jpg"),
            require("../assets/img/2-7.jpg"),
            require("../assets/img/2-8.jpg")
          ]
        },
        {
          name: "美图",
          des: "美图",
          imgsList: [require("../assets/img/4-1.jpg")]
        },
        {
          name: "风景",
          des: "优美风景",
          imgsList: [
            require("../assets/img/5-1.jpg"),
            require("../assets/img/5-2.jpg")
          ]
        },
        {
          name: "One Piece",
          des: "オレは海賊王になる男だ",
          imgsList: [
            require("../assets/img/6-1.jpg"),
            require("../assets/img/6-2.jpg"),
            require("../assets/img/6-3.jpg"),
            require("../assets/img/6-4.jpg"),
            require("../assets/img/6-5.jpg"),
            require("../assets/img/6-6.jpg"),
            require("../assets/img/6-7.jpg"),
            require("../assets/img/6-8.jpg")
          ]
        },
        {
          name: "中山陵、博物馆",
          des: "2019年南京中山陵，博物馆之旅",
          imgsList: [
            require("../assets/img/7-1.jpg"),
            require("../assets/img/7-2.jpg"),
            require("../assets/img/7-3.jpg"),
            require("../assets/img/7-4.jpg"),
            require("../assets/img/7-5.jpg"),
            require("../assets/img/7-6.jpg"),
            require("../assets/img/7-7.jpg"),
            require("../assets/img/7-8.jpg")
          ]
        },
        {
          name: "游乐园",
          des: "2019年暑期游乐园之旅",
          imgsList: [
            require("../assets/img/8-1.jpg"),
            require("../assets/img/8-2.jpg"),
            require("../assets/img/8-3.jpg"),
            require("../assets/img/8-4.jpg"),
            require("../assets/img/8-5.jpg"),
            require("../assets/img/8-6.jpg"),
            require("../assets/img/8-7.jpg"),
            require("../assets/img/8-8.jpg")
          ]
        }
      ],
      flag: false,
      idx1: 0, //第几个li
      idx2: 0 //第几张图,
    };
  },
  methods: {
    show(index) {
      this.flag = !this.flag;
      this.idx1 = index;
    },
    next() {
      if (this.idx2 < this.list[this.idx1].imgsList.length - 1) {
        this.idx2++;
      }
    },
    pre() {
      if (this.idx2 > 0) {
        this.idx2--;
      }
    }
  }
};
</script>
<style scoped>
@import "../assets/css/common.css";
@import "../assets/css/Photos.css";
</style>


