var count = 1;//定义变量，用来是第几张图片
var reg = /[\-][0-9]/g;
window.onload = function () {
    $(function () {
        $(".main li").hover(function () {
            $(this).toggleClass("fil").find(".des").toggleClass("opa").siblings("img").toggleClass("amplification");
        }).click(function () {
            //点击获取图片url
            var src = $(this).find("img")[0].src;
            //将url添加给大图片
            $(".detail").css("display", "block").find("img").attr("src", src);
        });
        //关闭大图
        $(".del").click(function () {
            $(this).parents(".detail").css("display", "none");
            count = 1;
        });
        //右边按钮
        $(".btn_r").click(function () {
            count++;
            //判断书否为第八张图片，是则求余
            //替换图片的-和后面的数字
            var src = $(this).siblings("img")[0].src;
            CheckImgExists(src);
            if (count > 8) {
                count = count % 8;
            }
            src = $(this).siblings("img")[0].src.replace(reg, "-" + count); //(count%8+1)
            $(this).siblings("img").attr("src", src);
            // CheckImgExists(src)
        });
        //左边按钮
        $(".btn_l").click(function () {
            count--;
            //判断是否为第一张图片，是则跳到第八张
            if (count <= 0) {
                count = 8;
            }
            var src = $(this).siblings("img")[0].src.replace(reg, "-" + count); //(count%8+1)
            $(this).siblings("img").attr("src", src);
        })
    })


    function CheckImgExists(imgurl) {
        var ImgObj = new Image(); //判断图片是否存在
        ImgObj.src = imgurl;
        //存在图片
        if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
            return true;
        } else {
            return count++;
        }
    }

}


